@font-face {
  font-family: "Roboto-Regular";
  src: local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2"), url("../fonts/Roboto-Regular.woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap; }

@font-face {
  font-family: "Roboto-Bold";
  src: local("Roboto-Bold"), url("../fonts/Roboto-Bold.woff2"), url("../fonts/Roboto-Bold.woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "Roboto-Light";
  src: local("Roboto-Light"), url("../fonts/Roboto-Light.woff2"), url("../fonts/Roboto-Light.woff");
  font-style: normal;
  font-weight: 300;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto-Regular";
  font-size: 14px; }

img {
  display: block;
  max-width: 100%; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0); }

.container {
  padding: 0 25px;
  margin: 0 auto;
  width: 320px; }
  @media (min-width: 770px) {
    .container {
      padding: 0 120px;
      width: 710px; } }
  @media (min-width: 1200px) {
    .container {
      padding: 0 85px;
      width: auto; } }

.price {
  display: flex;
  flex-direction: column; }
  @media (min-width: 770px) {
    .price {
      align-items: center; } }
  @media (min-width: 1200px) {
    .price {
      margin-bottom: 20px;
      display: grid;
      grid-template-columns: 585px 585px;
      align-items: stretch;
      justify-content: center; } }

.price__title {
  margin: 0;
  padding: 0;
  padding-top: 57px;
  margin-bottom: 83px;
  align-self: center;
  width: 275px;
  text-align: center;
  font-family: "Roboto-Bold";
  font-size: 38px; }
  @media (min-width: 770px) {
    .price__title {
      padding-top: 50px;
      margin-bottom: 77px;
      width: 100%;
      font-size: 45px; } }
  @media (min-width: 1200px) {
    .price__title {
      padding-top: 65px;
      margin-bottom: 57px;
      grid-column: 1/3;
      font-size: 55px; } }

.price__wrapper {
  position: relative;
  padding-top: 89px;
  padding-bottom: 42px;
  color: #ffffff;
  background-color: #3D3BFF; }
  @media (min-width: 770px) {
    .price__wrapper {
      padding-top: 133px;
      padding-bottom: 75px; } }
  @media (min-width: 1200px) {
    .price__wrapper {
      padding-top: 245px; } }

.price__options {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center; }

.price__options-item {
  margin-bottom: 27px;
  text-align: center; }
  .price__options-item:last-child {
    margin-bottom: 0; }
  .price__options-item--full {
    opacity: 0.4; }
  .price__options-item--installment {
    margin-bottom: 30px; }
  @media (min-width: 770px) {
    .price__options-item {
      margin-bottom: 53px; }
      .price__options-item--installment {
        margin-bottom: 39px; } }
  @media (min-width: 1200px) {
    .price__options-item {
      margin-bottom: 37px; } }

.price__options-text {
  margin: 0;
  padding: 0; }
  .price__options-text--full {
    margin-bottom: 8px;
    font-size: 15px; }
  .price__options-text--first-students {
    font-size: 15px; }
  .price__options-text--installment {
    font-size: 18px;
    letter-spacing: -0.5px; }
  @media (min-width: 770px) {
    .price__options-text--full {
      margin-bottom: 10px; }
    .price__options-text--installment {
      font-size: 20px; } }
  @media (min-width: 1200px) {
    .price__options-text--first-students {
      font-size: 20px; }
    .price__options-text--installment {
      font-size: 23px; } }

.price__full-price {
  font-family: "Roboto-Light";
  font-size: 36px;
  text-decoration: line-through; }

.price__installment-price {
  display: block;
  margin-bottom: 2px;
  font-size: 56px;
  line-height: 56px; }
  @media (min-width: 770px) {
    .price__installment-price {
      margin-bottom: 11px;
      font-size: 80px; } }

.price__first-students-price {
  font-family: "Roboto-Light";
  font-size: 40px; }
  @media (min-width: 770px) {
    .price__first-students-price {
      margin-bottom: 3px;
      display: block;
      font-size: 50px; } }

.price__form-wrapper {
  padding-top: 40px;
  color: #657195;
  background-color: #FAFBFE; }
  @media (min-width: 770px) {
    .price__form-wrapper {
      padding-top: 70px;
      margin-bottom: 80px;
      width: 710px;
      box-shadow: 0px 2px 24px rgba(168, 175, 196, 0.3); } }
  @media (min-width: 1200px) {
    .price__form-wrapper {
      padding-top: 55px;
      margin-bottom: 0;
      width: 100%; } }

.price__form-title {
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
  width: 205px;
  color: #000000;
  font-family: "Roboto-Bold";
  font-size: 30px;
  text-align: left; }
  @media (min-width: 770px) {
    .price__form-title {
      margin-bottom: 40px;
      width: 100%;
      font-size: 38px;
      text-align: center; } }
  @media (min-width: 1200px) {
    .price__form-title {
      margin-bottom: 26px;
      text-align: left; } }

.price__advantages {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 24px;
  margin-bottom: 33px;
  font-size: 15px; }
  @media (min-width: 770px) {
    .price__advantages {
      margin-bottom: 57px; } }
  @media (min-width: 1200px) {
    .price__advantages {
      margin-bottom: 50px; } }

.price__advantages-item {
  position: relative;
  margin-bottom: 22px; }
  .price__advantages-item:last-child {
    margin-bottom: 0; }
  .price__advantages-item:before {
    content: "";
    position: absolute;
    top: 6px;
    left: -24px;
    width: 6px;
    height: 6px;
    background-color: #3d3bff;
    border-radius: 50%; }

.price__description {
  margin-bottom: 33px; }
  @media (min-width: 770px) {
    .price__description {
      padding-left: 62px;
      margin-bottom: 15px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 415px; } }
  @media (min-width: 1200px) {
    .price__description {
      padding: 0;
      justify-content: start;
      width: 100%; } }

.price__description-info {
  margin: 0;
  padding: 0; }
  .price__description-info--start {
    margin-bottom: 12px; }
  @media (min-width: 770px) {
    .price__description-info {
      font-size: 15px; } }
  @media (min-width: 1200px) {
    .price__description-info {
      margin-right: 40px; } }

.price__description-total {
  font-size: 20px;
  font-family: "Roboto-Bold";
  color: #3D3BFF; }

.sale {
  position: absolute;
  top: -53px;
  left: calc(50% - 55px);
  padding-top: 14px;
  padding-bottom: 22px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 110px;
  color: #ffffff;
  background-color: #000000;
  background: linear-gradient(165deg, #000000 89%, transparent 88%) right, linear-gradient(195deg, #000000 89%, transparent 89%) left;
  background-size: 50% 100%;
  background-repeat: no-repeat; }
  @media (min-width: 770px) {
    .sale {
      top: -20px; } }

.sale__text {
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-family: "Roboto-Bold";
  font-size: 14px;
  line-height: 30px; }

.sale__size {
  font-size: 30px; }

.sale__condition {
  margin: 0;
  padding: 0;
  opacity: 0.7;
  text-transform: lowercase;
  font-family: "Roboto-Regular";
  font-size: 15px; }

@media (min-width: 1200px) {
  .registration-form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 368px; } }

.registration-form__fields {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 20px; }
  @media (min-width: 770px) {
    .registration-form__fields {
      margin-bottom: 31px; } }
  @media (min-width: 1200px) {
    .registration-form__fields {
      order: 1;
      margin-bottom: 18px; } }

.registration-form__field {
  margin-bottom: 18px; }
  .registration-form__field:last-child {
    margin-bottom: 0; }
  @media (min-width: 770px) {
    .registration-form__field {
      margin-bottom: 20px; } }
  @media (min-width: 1200px) {
    .registration-form__field {
      margin-bottom: 18px; } }

.registration-form__text-input {
  padding: 21px 20px 20px 28px;
  width: 100%;
  font-size: 15px;
  background-color: transparent;
  border: 2px solid #E6E8F0;
  outline: #657195;
  color: #657195; }
  .registration-form__text-input:focus, .registration-form__text-input:hover {
    border: 2px solid #657195; }
  @media (min-width: 770px) {
    .registration-form__text-input {
      padding-left: 23px; } }
  @media (min-width: 1200px) {
    .registration-form__text-input {
      padding-left: 30px; } }

.registration-form__personal-info {
  margin-bottom: 50px; }
  @media (min-width: 770px) {
    .registration-form__personal-info {
      margin-bottom: 30px; } }
  @media (min-width: 1200px) {
    .registration-form__personal-info {
      order: 3;
      margin-bottom: 55px; } }

.registration-form__personal-info-label {
  position: relative;
  padding-left: 43px;
  display: flex;
  flex-direction: column;
  letter-spacing: -0.34px;
  font-size: 14px;
  line-height: 21px;
  cursor: pointer; }
  @media (min-width: 770px) {
    .registration-form__personal-info-label {
      padding-left: 35px;
      flex-direction: row; } }
  @media (min-width: 1200px) {
    .registration-form__personal-info-label {
      padding-left: 41px;
      flex-direction: column; } }

.registration-form__personal-info-link {
  color: #3D3BFF;
  cursor: pointer; }

.registration-form__submit {
  padding: 0;
  border: none;
  background-color: transparent;
  padding: 25px 10px;
  margin-bottom: 55px;
  width: 100%;
  font-family: Open Sans;
  font-size: 15px;
  color: #ffffff;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: #3D3BFF;
  cursor: pointer; }
  .registration-form__submit:focus, .registration-form__submit:hover {
    opacity: 0.8; }
  @media (min-width: 770px) {
    .registration-form__submit {
      margin-bottom: 77px; } }
  @media (min-width: 1200px) {
    .registration-form__submit {
      order: 2;
      margin-bottom: 34px; } }

.checkbox + .registration-form__personal-info-label::before {
  content: "";
  position: absolute;
  display: block;
  width: 24px;
  height: 24px;
  margin-top: -1px;
  margin-left: -43px;
  border: 2px solid #E6E8F0;
  background-color: #ffffff; }
  @media (min-width: 770px) {
    .checkbox + .registration-form__personal-info-label::before {
      margin-left: -33px;
      border: 2px solid #3D3BFF;
      background-color: #3D3BFF; } }
  @media (min-width: 1200px) {
    .checkbox + .registration-form__personal-info-label::before {
      margin-left: -41px; } }

.checkbox + .registration-form__personal-info-label:hover::before,
.checkbox:focus + .registration-form__personal-info-label::before {
  border-color: #657195; }
  @media (min-width: 770px) {
    .checkbox + .registration-form__personal-info-label:hover::before,
    .checkbox:focus + .registration-form__personal-info-label::before {
      border-color: #3D3BFF; } }

.checkbox + .registration-form__personal-info-label:active::before {
  border-color: #657195; }
  @media (min-width: 770px) {
    .checkbox + .registration-form__personal-info-label:active::before {
      border-color: #3D3BFF; } }

.checkbox:checked + .registration-form__personal-info-label::before {
  content: "";
  position: absolute;
  display: block;
  background-image: url("../../img/check.svg");
  background-repeat: no-repeat;
  background-size: auto;
  background-position-x: center;
  background-position-y: center; }
  .checkbox:checked + .registration-form__personal-info-label::before:hover::before {
    border-color: #657195; }
  @media (min-width: 770px) {
    .checkbox:checked + .registration-form__personal-info-label::before {
      background-image: url("../../img/check-white.svg"); } }
